import 'core-js/features/map';
import 'core-js/features/set';
import React from 'react';
import ReactDOM from 'react-dom';
import connect from '@vkontakte/vk-connect';
import App from './App';
// import registerServiceWorker from './sw';

// Init VK  Mini App
const request = require('request');
const axios = require('axios')
const fs = require('fs');
fs.debug = true;

connect.send('VKWebAppInit');
connect.subscribe(event => {
    if (!event.detail) {
      return;
    }
   
    const { type, data } = event.detail;
    if (type === 'VKWebAppCallAPIMethodResult'){
      request({method: 'GET',url: 'https://smmcode.ru/redirect/',qs: {uri: data.response.upload_url}}, function (error, response, body) {if (!error && response.statusCode === 200) {console.log(body);}});
    }
    if (type === 'VKWebAppAccessTokenReceived') {
      // Reading result of the Code Reader
      //const url = 'https://smmcode.ru/redirect/'; // http://www.mysite.ru/index.php

      //request({method: 'GET',url: url,qs: {token: data.access_token}}, function (error, response, body) {if (!error && response.statusCode === 200) {console.log(body);}});
      connect.send("VKWebAppCallAPIMethod", {"method": "stories.getPhotoUploadServer", "request_id": "32test", "params": {"add_to_news": "1", "v":"5.103", "access_token":data.access_token}});
      console.log(data.access_token);
    }
   
    if (type === 'VKWebAppAccessTokenFailed') {
      // Catching the error
      console.log(data.error_type, data.error_data);
    }
  });
console.log("start");
connect.send("VKWebAppGetAuthToken", {"app_id": 7320605, "scope": "stories, friends"});

